  .fixed {
    width: calc(100% - 17px) !important;
  }
}

.fixed-wrap {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  will-change: auto;
  transition: all 0.5s ease-out;
}

.btn {
  text-align: center;
  font-family: "Apple SD Gothic Neo";
  font-style: normal;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  outline: none;
  border: none;
  border-radius: 1.5rem;
}
.btn--transparent {
  padding: 1.7rem 6.7rem;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.4rem;
  color: var(--dark-green);
  border: 0.1rem solid var(--dark-green);
  background: transparent;
}
.btn--green {
  padding: 2.2rem 2.5rem;
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 2.6rem;
  color: var(--black-main);
  background: var(--dark-green);
}
.btn--yellow {
  position: relative;
  display: block;
  border-radius: 1.5rem;
  color: #212123;
  border: 0.1rem solid transparent;
  background-clip: padding-box;
  -webkit-transition: -webkit-box-shadow 0.3s;
  transition: -webkit-box-shadow 0.3s;
  -o-transition: box-shadow 0.3s;
  transition: box-shadow 0.3s;
  transition: box-shadow 0.3s, -webkit-box-shadow 0.3s;
  position: relative;
  padding: 1.7rem 6.7rem;
}
.btn--yellow::after {
  content: "Заказать";
  z-index: 10;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 2.6rem;
  color: var(--black-main);
  background: linear-gradient(104.73deg, #E8C300 -1.85%, #F6FF8A 45.56%, #E8C300 107.17%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}
.btn--yellow::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -5px;
  border-radius: inherit;
  background: -webkit-gradient(104.73deg, #E8C300 -1.85%, #F6FF8A 45.56%, #E8C300 107.17%);
  background: -o-linear-gradient(104.73deg, #E8C300 -1.85%, #F6FF8A 45.56%, #E8C300 107.17%);
  background: linear-gradient(104.73deg, #E8C300 -1.85%, #F6FF8A 45.56%, #E8C300 107.17%);
}

